export default {
	data() {
		return {
			id: 0,
			detail: {}
		}
	},
	onLoad(options) {
		// 获取传递过来的 id
		this.id = options.id
		this.getNewsDetail()
	},
	methods: {
		async getNewsDetail() {
			const res = await this.$myRequest({
				url: '/api/getnew/'+ this.id
			})
			this.detail = res.data.message[0]
			console.log(this.detail)
		}
	}
}
